.list__nav-bar {
    display: flex;
    list-style: none;
    margin: 0; 
    padding: 0;
  }
  
  .list-item__nav-bar {
    text-align: center; 
    display: block;
    height: 100%;
  }

  //main-nav-bar
  .main-nav-bar--container {
    background: $orange;
    display: flex;
    justify-content: space-between;
    margin: 0; 
    height: 5rem;
    @media (max-width: $desktop-breakpoint) {
      height: 2rem;
    }
  }
  
  .main-nav-bar-button {
    background: $orange;
    color: $white;
    font-size: 1.1rem;
    height: 100%;
    padding: 1rem;
    &:hover{
        background: $dark-orange;
    }
  }
  
  .add-post-button {
    border-right: 0.1px solid $very-dark-orange;;
  }
  
  .main-nav-bar--image {
    background: $orange;
  }

  //secondary-nav-bar
  .secondary-nav-bar--container {
    background: white;
    border-bottom: $off-black .3px outset;
    display: flex;
    justify-content: space-between;
    margin: 0; 
    height: 3rem;
    @media (max-width: $desktop-breakpoint) {
      height: 2rem;
    }
  }

  .secondary-nav-bar-button {
    background: white;
    color: $off-black;
    font-size: 1.1rem;
    height: 100%;
    padding: 1rem;
    &:hover{
        background: $off-white;
    }
  }