* {
    box-sizing: border-box;
  }
  
  html {
    box-sizing: inherit;
    font-size: 62.5%;   
  }

  body {
    color: #363636;
    direction: rtl;
    display: block;
    font-family: Rubik,sans-serif;
    height: 100%;
    padding: 0;
    line-height: 16px;
    margin: 0;
    // font-size: 16px;
    -webkit-tap-highlight-color: transparent;
  }

  button {
    cursor: pointer;
    border: none;
  }
  
  button:disabled {
    cursor: default;
  }
